	module.exports = {"data":{"wordpressPost":{"title":"Divorce may be linked to higher risk of overweight/obesity among kids involved","content":"<p><a href=\"http://bmjopen.bmj.com/content/4/6/e004502.full\">Divorce may be linked to a higher risk of overweight and obesity among children affected by the marital split,</a> suggests research published in the on-line journal <a href=\"http://bmjopen.bmj.com/\">BMJ Open</a>.</p>\n<p>Boys may be especially prone to excess weight gain, the findings indicate.</p>\n<p>The researchers base their findings on a nationally representative sample of more than 3000 pupils attending 127 schools across Norway. All the children were part of the national 2010 Norwegian Child Growth Study.</p>\n<p>School nurses measured the height, weight, and waist circumference of the children whose average age was 8, to gauge general overweight, as defined by the International Obesity Task Force (IOTF), and (abdominal) obesity, as defined by a waist to height ratio of 0.5 or more.</p>\n<p>The results were stratified by gender and parents’ marital status &#8211; married, never married, to include co-habiting, single and separated, and divorced &#8211; taking account of influential factors, such as the mother’s educational attainment, ethnic origin and area of residence.</p>\n<p>Around one in five (19%) of the children was overweight or obese according to the IOTF definition, while just under one in 10 (8.9%) was (abdominally) obese.</p>\n<p>Overall, significantly more of the 1537 girls were overweight or obese than the 1629 boys, but there were no differences in the prevalence of (abdominal) obesity.</p>\n<p>More of the children whose parents were categorised as divorced were overweight or obese than those whose parents remained married.</p>\n<p>They were 54% more likely to be overweight/obese and 89% more likely to be (abdominally) obese. Children whose parents had never married had a similar prevalence of overweight and obesity to those with married parents.</p>\n<p>The findings held true even after taking account of other possible explanatory factors.</p>\n<p>But these differences were generally larger for boys whose parents were divorced. They were 63% more likely to be generally overweight/obese than boys whose parents were married. And they were 104% more likely to be abdominally obese.</p>\n<p>The absolute differences were 9.9 and 7.4 percentage points, respectively.</p>\n<p>The same pattern was seen among girls, but the associations were less marked and, unlike the boys, not statistically significant.</p>\n<p>The authors caution that the design of their study does not provide a basis for establishing cause and effect. Furthermore, they were unable to glean how long parents had been divorced, nor were they able to include lifestyle factors such as the children’s normal diet and exercise regime.</p>\n<p>But the associations they found are consistent with findings from other studies they say.</p>\n<p>Possible explanations for the link could include less time spent on domestic tasks such as cooking; an over-reliance on unhealthier convenience foods and ready meals; and lower household income.</p>\n<p>The emotional fall-out of a divorce and resulting stress generated by disruptions in the parent-child relationship, ongoing conflict between the exes, moving home and the need to create new social networks, might also explain the findings, the authors suggest. And boys might just be more vulnerable, they say<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"90baf7c6-84d9-5f22-9be1-723aae13b1b7"}}

